.kMapUe-content-spin {
    width: 100%;
    height: 100%;

    .arco-spin-children {
        height: 100%;

        &::after {
            background-color: transparent;
        }
    }

    .kMapUe-content-wrap {
        width: 100%;
        height: 100%;
    }
}

.ue-preview-box {
    width: 100%;
    height: 100%;
    background-color: #171a21;
    display: flex;
    align-items: center;

    .animated-underline {
        color: #5475FF;
        cursor: pointer;
        margin-left: 4px;
    }
}