.home-box {
  height: 100%;
  overflow: hidden;
  position: relative;

  .header-wrap {
    position: absolute;
    top: 0;
    height: 119px;
    left: 0;
    right: 0;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    user-select: none;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 257px;
      background-image: url(~@/assets/img/mask-top.png);
      background-repeat: no-repeat;
      background-size: 100% 100%;
      transform: rotate(180deg);
      z-index: -1;
      pointer-events: none;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 115px;
      background-image: url(~@/assets/img/header.png);
      background-repeat: no-repeat;
      background-size: 100% 100%;
      z-index: 10;
      pointer-events: none;
    }

    img {
      height: 36px;
      margin-top: -10px;
      position: absolute;
      z-index: 110;
    }
  }

  .top-wrap {
    position: absolute;
    left: 50%;
    top: 130px;
    transform: translateX(-50%);
    z-index: 100;

    img {
      height: 70px;
    }
  }

  .left-mask {
    width: 500px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 25px;
    z-index: 90;
    background-image: url(~@/assets/img/mask-left.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    pointer-events: none;
  }

  .right-mask {
    width: 500px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 25px;
    z-index: 90;
    background-image: url(~@/assets/img/mask-left.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    transform: rotate(180deg);
    pointer-events: none;
  }

  .left-side {
    width: 500px;
    position: absolute;
    top: 30px;
    left: 10px;
    bottom: 25px;
    background-image: url(~@/assets/img/left-bg.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    z-index: 100;
    padding: 46px 0 40px 26px;
    outline: none;
    user-select: none;

    img {
      width: 424px;
    }
  }

  .right-side {
    width: 500px;
    position: absolute;
    top: 30px;
    right: 10px;
    bottom: 25px;
    background-image: url(~@/assets/img/right-bg.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    z-index: 100;
    padding: 46px 26px 40px 0;
    text-align: right;
    outline: none;
    user-select: none;

    img {
      width: 418px;
    }
  }

  .footer-mask {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 90;
    height: 257px;
    background-image: url(~@/assets/img/mask-top.png);
    background-repeat: no-repeat;
    background-position: bottom center;
    background-size: 100% 100%;
    pointer-events: none;
  }

  .footer-wrap {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    min-height: 44px;
    // background-image: url(~@/assets/img/footer.png);
    background-repeat: no-repeat;
    background-position: bottom center;
    background-size: 100% 44px;
    display: flex;
    justify-content: center;
    outline: none;
    user-select: none;

    .anbao {
      position: absolute;
      bottom: 30px;
      left: 20%;
      z-index: 1000;
    }

    .sprh {
      position: absolute;
      bottom: 30px;
      right: 20%;
      z-index: 1000;
    }

    .nav-list {
      display: flex;
      margin-bottom: 30px;

      .nav {
        width: 112px;
        height: 100px;
        cursor: pointer;
        outline: none;

        &:hover,
        &.active {
          .pic-hover {
            display: block;
          }

          .pic {
            display: none;
          }
        }

        .pic-hover {
          display: none;
        }

        img {
          width: 100%;
        }
      }
    }

    .nav-wrap {
      width: 1328px;
      height: 240px;

      position: relative;
      color: #fff;

      &::after {
        content: '';
        height: 362px;
        width: 1328px;
        bottom: 0;
        left: 0;
        background-image: url(~@/assets/img/nav-bg.png);
        background-repeat: no-repeat;
        background-position: bottom center;
        background-size: 100% 100%;
        position: absolute;
        pointer-events: none;
      }

      .nav {
        width: 120px;
        height: 120px;
        cursor: pointer;
        outline: none;
        color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        z-index: 10;

        .nav-con {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          z-index: 100;
        }

        &:nth-child(1) {
          bottom: -6px;
          left: 286px;

          .nav-con {
            transform: rotate(-35deg);
            transform-origin: center;
            padding-top: 40px;
            padding-left: 5px;
          }

          &:hover,
          &.active {
            &::after {
              content: '';
              position: absolute;
              width: 185px;
              height: 107px;
              background-image: url(~@/assets/img/nav-active1.png);
              background-repeat: no-repeat;
              background-position: bottom center;
              background-size: 100% 100%;
            }
          }
        }

        &:nth-child(2) {
          bottom: 30px;
          left: 396px;

          .nav-con {
            transform: rotate(-25deg);
            transform-origin: center;
            padding-top: 0;
          }

          &:hover,
          &.active {
            &::after {
              content: '';
              position: absolute;
              width: 141px;
              height: 130px;
              background-image: url(~@/assets/img/nav-active2.png);
              background-repeat: no-repeat;
              background-position: bottom center;
              background-size: 100% 100%;
            }
          }
        }

        &:nth-child(3) {
          bottom: 62px;
          left: 497px;

          .nav-con {
            transform: rotate(-11deg);
            transform-origin: center;
            padding-top: 9px;
          }

          &:hover,
          &.active {
            &::after {
              content: '';
              position: absolute;
              width: 122px;
              height: 123px;
              background-image: url(~@/assets/img/nav-active3.png);
              background-repeat: no-repeat;
              background-position: bottom center;
              background-size: 100% 100%;
            }
          }
        }

        &:nth-child(4) {
          bottom: 74px;
          left: 605px;

          .nav-con {
            transform: rotate(2deg);
            transform-origin: center;
            padding-top: 15px;
          }

          &:hover,
          &.active {
            &::after {
              content: '';
              position: absolute;
              width: 113px;
              height: 117px;
              background-image: url(~@/assets/img/nav-active4.png);
              background-repeat: no-repeat;
              background-position: bottom center;
              background-size: 100% 100%;
            }
          }
        }

        &:nth-child(5) {
          bottom: 56px;
          left: 709px;

          .nav-con {
            transform: rotate(13deg);
            transform-origin: center;
            padding-top: 10px;
          }

          &:hover,
          &.active {
            &::after {
              content: '';
              position: absolute;
              width: 122px;
              height: 123px;
              background-image: url(~@/assets/img/nav-active5.png);
              background-repeat: no-repeat;
              background-position: bottom center;
              background-size: 100% 100%;
            }
          }
        }

        &:nth-child(6) {
          bottom: 24px;
          left: 798px;

          .nav-con {
            transform: rotate(24deg);
            transform-origin: center;
            padding-top: 10px;
          }

          &:hover,
          &.active {
            &::after {
              content: '';
              position: absolute;
              width: 126px;
              height: 126px;
              background-image: url(~@/assets/img/nav-active6.png);
              background-repeat: no-repeat;
              background-position: bottom center;
              background-size: 100% 100%;
            }
          }
        }

        &:nth-child(7) {
          bottom: -8px;
          left: 897px;

          .nav-con {
            transform: rotate(37deg);
            transform-origin: center;
            padding-top: 50px;
            padding-right: 10px;
          }

          &:hover,
          &.active {
            &::after {
              content: '';
              position: absolute;
              width: 181px;
              height: 107px;
              background-image: url(~@/assets/img/nav-active7.png);
              background-repeat: no-repeat;
              background-position: bottom center;
              background-size: 100% 100%;
            }
          }
        }

        .tit {
          text-align: center;
        }

        &:hover,
        &.active {
          // background-image: url(~@/assets/img/nav-active.png);
          // background-repeat: no-repeat;
          // background-position: bottom center;
          // background-size: 146px 142px;

          .pic-hover {
            display: block;
          }

          .pic {
            display: none;
          }
        }

        .pic-hover {
          display: none;
        }

        img {
          width: 40px;
        }
      }

      .nav-child-wrap {
        position: absolute;
        bottom: 0;
        display: flex;
        font-size: 15px;
        left: 106px;

        .nav-child-li {
          width: 200px;
          height: 46px;
          text-align: center;
          position: absolute;
          cursor: pointer;
          z-index: 100;
          color: #fff;
          text-shadow: 2px 2px 4px #ff9800;

          label {
            padding: 0 2px;
            cursor: pointer;
          }

          @keyframes moveArrow {
            0% {
              margin: 0; // 
            }

            100% {
              margin: 4px; // 10px
            }
          }

          .arrow {
            animation: moveArrow 0.8s infinite alternate;
          }

          .arrow-left {
            transform: rotate(180deg);
          }

          &:nth-child(1) {
            bottom: 53px;
            left: 105px;
            transform: rotate(-38deg);
          }

          &:nth-child(2) {
            bottom: 137px;
            left: 268px;
            transform: rotate(-17deg);
          }

          &:nth-child(3) {
            bottom: 164px;
            left: 444px;
            transform: rotate(-1deg);
          }

          &:nth-child(4) {
            bottom: 136px;
            left: 631px;
            transform: rotate(17deg);
          }

          &:nth-child(5) {
            bottom: 55px;
            left: 792px;
            transform: rotate(38deg);
          }

          &.active,
          &:hover {
            background-image: url(~@/assets/img/child-bg.svg);
            background-repeat: no-repeat;
            background-size: 100% 100%;
          }
        }
      }
    }
  }
}