.to-w (@prop, @value) {
  @result: (@value / 19.2) * 1%;
  @{prop}: @result;
}

.to-h (@prop, @value) {
  @result: (@value / 10.02) * 1%;
  @{prop}: @result;
}

.ant-spin-nested-loading {
  width: 100%;
  height: 100%;

  .ant-spin-container {
    width: 100% !important;
    height: 100% !important;
  }

  .ant-spin {
    max-height: 100% !important;
  }

  .map-content-wrap {
    width: 100%;
    height: 100%;
  }
}
